  createWebauthConfig(username, displayName) {
    const challenge = new Uint8Array(32)
    const userId = new Uint8Array(32)
    crypto.getRandomValues(challenge)
    crypto.getRandomValues(userId)

    const options = {
      publicKey: {
        challenge: challenge,

        rp: {
          name: "Webauthn Play",
          id: "webauthnplay.herokuapp.com",
        },

        user: {
          id: userId,
          name: username,
          displayName: displayName,
        },

        attestation: "direct",

        authenticatorSelection: { authenticatorAttachment: "platform" },
        pubKeyCredParams: [
          {
            type: "public-key",
            alg: -7, // "ES256" IANA COSE Algorithms registry
          },
        ],
      },
    }

    this.setState({
      challenge: challenge,
      user: { id: userId, name: username, displayName: displayName },
      options: options,
    })
    return options
  }

  createWebauthGetConfig() {
    const { challenge, user, response } = this.state
    if (response) {
      return {
        publicKey: {
          challenge: challenge,
          allowCredientails: [
            {
              type: "public-key",
              id: response.rawId,
              transports: ["usb", "ble", "nfc", "internal"],
            },
          ],
        },
      }
    }
  }

  parseCreateCredentialResponse(credential) {
    // Parse Client Data
    const utf8Decoder = new TextDecoder("utf-8")
    const decodedClientData = utf8Decoder.decode(
      credential.response.clientDataJSON
    )

    // parse the string as an object
    const clientDataObj = JSON.parse(decodedClientData)

    // Parse attestation Object
    const decodedAttestationObject = cbor.decodeAllSync(
      credential.response.attestationObject
    )[0]

    const { authData } = decodedAttestationObject

    // get the length of the credential ID
    const dataView = new DataView(new ArrayBuffer(2))
    const idLenBytes = authData.slice(53, 55)
    idLenBytes.forEach((value, index) => dataView.setUint8(index, value))
    const credentialIdLength = dataView.getUint16()

    // get the credential ID
    const credentialId = authData.slice(55, 55 + credentialIdLength)

    // get the public key object
    const publicKeyBytes = authData.slice(55 + credentialIdLength)

    // the publicKeyBytes are encoded again as CBOR
    const publicKeyObject = cbor.decode(publicKeyBytes.buffer)

    return { clientDataObj, publicKeyObject }
  }

  parseGetCredentialResponse(data) {
    const clientData = JSON.parse(
      base64url.decode(data.response.clientDataJSON)
    )
    const authenticatorData = parseAuthenticatorData(
      base64url.toBuffer(data.response.authenticatorData)
    )
    const userHandle = base64url.decode(data.response.userHandle)
    return { clientData, authenticatorData, userHandle }
  }

